import menu_discard_0 from "url:../resources/sounds/menu_discard_0.mp3";
import menu_discard_1 from "url:../resources/sounds/menu_discard_1.mp3";

import upgrade_bought_0 from "url:../resources/sounds/upgrade_bought_0.mp3";
import upgrade_bought_1 from "url:../resources/sounds/upgrade_bought_1.mp3";
import upgrade_bought_2 from "url:../resources/sounds/upgrade_bought_2.mp3";
import upgrade_bought_3 from "url:../resources/sounds/upgrade_bought_3.mp3";
import upgrade_bought_4 from "url:../resources/sounds/upgrade_bought_4.mp3";
import upgrade_bought_5 from "url:../resources/sounds/upgrade_bought_5.mp3";

import y0_upgrade_bought from "url:../resources/sounds/y0_upgrade_bought.mp3";

import menu_select_0 from "url:../resources/sounds/menu_select_0.mp3";
import menu_select_1 from "url:../resources/sounds/menu_select_1.mp3"
import menu_select_2 from "url:../resources/sounds/menu_select_2.mp3"
import menu_select_3 from "url:../resources/sounds/menu_select_3.mp3"
import menu_select_4 from "url:../resources/sounds/menu_select_4.mp3"
import menu_select_5 from "url:../resources/sounds/menu_select_5.mp3"
import menu_select_6 from "url:../resources/sounds/menu_select_6.mp3"

import rock_hit_0 from "url:../resources/sounds/rock_hit_0.mp3";
import rock_hit_1 from "url:../resources/sounds/rock_hit_1.mp3";
import rock_hit_2 from "url:../resources/sounds/rock_hit_2.mp3";
import rock_hit_3 from "url:../resources/sounds/rock_hit_3.mp3";
import rock_hit_4 from "url:../resources/sounds/rock_hit_4.mp3";
import rock_hit_5 from "url:../resources/sounds/rock_hit_5.mp3";
import rock_hit_6 from "url:../resources/sounds/rock_hit_6.mp3";

import boost from "url:../resources/sounds/boost.mp3";

import bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix from "url:../resources/sounds/bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix.mp3";

import {getRandomInt} from "../utils/math";
import config from "../config";

const soundMap = {
  y0_upgrade_bought: new Audio(y0_upgrade_bought),
  boost: new Audio(boost),
  bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix: new Audio(bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix)
};

const rockHitMap = {
  rock_hit_0: new Audio(rock_hit_0),
  rock_hit_1: new Audio(rock_hit_1),
  rock_hit_2: new Audio(rock_hit_2),
  rock_hit_3: new Audio(rock_hit_3),
  rock_hit_4: new Audio(rock_hit_4),
  rock_hit_5: new Audio(rock_hit_5),
  rock_hit_6: new Audio(rock_hit_6),
}

const menuSelectMap = {
  menu_select_0: new Audio(menu_select_0),
  menu_select_1: new Audio(menu_select_1),
  menu_select_2: new Audio(menu_select_2),
  menu_select_3: new Audio(menu_select_3),
  menu_select_4: new Audio(menu_select_4),
  menu_select_5: new Audio(menu_select_5),
  menu_select_6: new Audio(menu_select_6),
}

const menuDiscardMap = {
  menu_discard_0: new Audio(menu_discard_0),
  menu_discard_1: new Audio(menu_discard_1),
}

const upgradeBoughtMap = {
  upgrade_bought_0: new Audio(upgrade_bought_0),
  upgrade_bought_1: new Audio(upgrade_bought_1),
  upgrade_bought_2: new Audio(upgrade_bought_2),
  upgrade_bought_3: new Audio(upgrade_bought_3),
  upgrade_bought_4: new Audio(upgrade_bought_4),
  upgrade_bought_5: new Audio(upgrade_bought_5),
}

export default function (sound) {
  let audioFile;
  switch (sound) {
    case "rock_hit":
      audioFile = rockHitMap[`rock_hit_${getRandomInt(0, Object.keys(rockHitMap).length)}`];
      audioFile.volume = getRandomInt(5, 11) / 10;
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.play();
      break;
    case "menu_select":
      audioFile = menuSelectMap[`menu_select_${getRandomInt(0, Object.keys(menuSelectMap).length)}`];
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.play();
      break;
    case "menu_discard":
      audioFile = menuDiscardMap[`menu_discard_${getRandomInt(0, Object.keys(menuDiscardMap).length)}`];
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.play();
      break;
    case "upgrade_bought":
      audioFile = upgradeBoughtMap[`upgrade_bought_${getRandomInt(0, Object.keys(upgradeBoughtMap).length)}`];
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.play();
      break;
    case "bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix":
      audioFile = soundMap[sound];
      if (!audioFile) {
        console.log("Audio file not found!");
        return;
      }
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.loop = true;
      audioFile.volume = config.bgm_volume;
      audioFile.play();
      break;
    default:
      audioFile = soundMap[sound];
      if (!audioFile) {
        console.log("Audio file not found!");
        return;
      }
      audioFile.pause();
      audioFile.currentTime = 0;
      audioFile.play();
      break;
  }
}