import translate from "./translations/translations";
import config from "./config";
import upgrades from "./data/upgrades";
import incText from "./components/incText";
import dustParticle from "./components/dustParticle";
import workers from "./data/workers";
import notify from "./components/notification";
import polyfills from "./utils/polyfills";
import playSound from "./components/playSound";
import fancyNotification from "./components/fancyNotification";
import saveSystem from "./components/saveSystem";
import heatPanel from "./components/heatPanel";

const store = {};

function init() {
  initStore();
  polyfills();
  saveSystem.loadProgress(store);

  const background = document.createElement("div");
  background.className = "background";
  document.body.appendChild(background);

  const app = document.createElement("div");
  app.id = "app";
  app.className = "app noselect";
  store.elems.app = app;

  const container = document.createElement("div");
  container.className = "container";

  const leftPanel = document.createElement("div");
  leftPanel.className = "left-panel";

  const title = document.createElement("div");
  title.className = "title";
  title.innerText = translate("title");

  const moneyBlock = document.createElement("div");
  moneyBlock.className = "money-block";
  store.elems.moneyBlock = moneyBlock;

  const moneyBlockText = document.createElement("span");
  moneyBlockText.className = "money-block-text";
  moneyBlockText.innerText = translate("money_intro");

  const moneyBlockCount = document.createElement("span");
  moneyBlockCount.className = "money-block-count";
  store.elems.moneyBlockCount = moneyBlockCount;

  const mineBlock = document.createElement("div");
  mineBlock.className = "mine-block";

  const mine = document.createElement("div");
  mine.className = "mine";
  store.elems.mine = mine;
  mine.setAttribute("data-action", "mine-click");

  const heatPanelElement = heatPanel.initHeatPanel(store);

  const rightPanel = document.createElement("div");
  rightPanel.className = "right-panel";

  const upgrades = document.createElement("div");
  upgrades.className = "upgrades";

  const upgradesTitle = document.createElement("div");
  upgradesTitle.className = "upgrades-title";
  upgradesTitle.innerText = translate("upgrades_title");

  const upgradesContainer = document.createElement("div");
  store.elems.upgradesContainer = upgradesContainer;
  upgradesContainer.className = "upgrades-container";
  upgradesContainer.appendChild(fillUpgrades());

  upgrades.appendChild(upgradesTitle);
  upgrades.appendChild(upgradesContainer);

  const workers = document.createElement("div");
  workers.className = "workers";

  const workersTitle = document.createElement("div");
  workersTitle.className = "workers-title";
  workersTitle.innerText = translate("workers_title");

  const workersContainer = document.createElement("div");
  store.elems.workersContainer = workersContainer;
  workersContainer.className = "workers-container";
  workersContainer.addEventListener("click", handleWorkersClick);
  workersContainer.appendChild(fillWorkers());

  workers.appendChild(workersTitle);
  workers.appendChild(workersContainer);

  mineBlock.appendChild(mine);
  leftPanel.appendChild(title);
  moneyBlock.appendChild(moneyBlockText);
  moneyBlock.appendChild(moneyBlockCount);
  leftPanel.appendChild(moneyBlock);
  leftPanel.appendChild(mineBlock)
  container.appendChild(leftPanel);
  container.appendChild(heatPanelElement);
  rightPanel.appendChild(upgrades);
  rightPanel.appendChild(workers);
  container.appendChild(rightPanel);
  app.appendChild(container);

  document.body.appendChild(app);
  setEventListeners();
  setInterval(loop, 1000 / config.fps);
  if (config.bgm) {
    playSound("bgm_HALL_OF_THE_MOUNTAIN_KING_Trap_Remix");
  }
  // after loading
  syncMoney();
  syncUpgrades();
  syncWorkers();
}

function initStore() {
  store.money = 0;
  store.perSecond = 0;
  store.elems = {};
  store.boughtUpgrades = [];
  store.boughtWorkers = [];
  store.heat = 0;
}

function setEventListeners() {
  store.elems.app.addEventListener("click", handleAppClick);
  // setting save progress function
  window.onbeforeunload = handleSaveProgress;
}

function handleSaveProgress() {
  saveSystem.saveProgress(store);
}

function syncUpgrades() {
  for (let i = 0; i < upgrades.length; i++) {
    const upgradeItem = document.querySelector(`.upgrade[data-id="${upgrades[i].id}"]`);
    upgradeItem.classList.remove("upgrade--bought");
  }
  for (let i = 0; i < store.boughtUpgrades.length; i++) {
    const upgradeItem = document.querySelector(`.upgrade[data-id="${store.boughtUpgrades[i]}"]`);
    upgradeItem.classList.add("upgrade--bought");
  }
}

function syncWorkers() {
  for (let i = 0; i < workers.length; i++) {
    const workerItem = document.querySelector(`.worker[data-id="${workers[i].id}"]`);
    workerItem.querySelector(".worker-quantity").innerText = 0;
    workerItem.querySelector(".worker-price").innerText = `${translate("tooltip_price")} ${workers[i].initialPrice}`;
  }
  for (let i = 0; i < store.boughtWorkers.length; i++) {
    const worker = workers.find((w) => w.id === store.boughtWorkers[i].id);
    const newPrice = Math.floor(worker.initialPrice * Math.pow(config.priceIncrement, store.boughtWorkers[i].quantity + 1));
    const workerItem = document.querySelector(`.worker[data-id="${store.boughtWorkers[i].id}"]`);
    workerItem.querySelector(".worker-quantity").innerText = store.boughtWorkers[i].quantity;
    workerItem.querySelector(".worker-price").innerText = `${translate("tooltip_price")} ${newPrice}`;
  }
}

function syncMoney() {
  document.querySelector(".money-block-count").innerText =
    `${Math.floor(store.money)} (${Number((store.perSecond).toFixed(1))} ${translate("per_second")})`;
}

function handleAppClick(event) {
  const action = event.target.getAttribute("data-action");
  switch (action) {
    case "mine-click":
      handleMineClick(event);
      break;
    case "buy-upgrade":
      handleBuyUpgrade(+event.target.getAttribute("data-id"));
      break;
  }
}

function handleMineClick(event) {
  let increment = config.initialIncrement;
  for (let upgradeId of store.boughtUpgrades) {
    const upg = upgrades.find(upgrade => upgrade.id === upgradeId)
    if (upg && upg.inc) {
      increment += upg.inc;
    }
  }
  store.money += increment;
  store.elems.mine.style.setProperty("transform", "scale(1.2)");
  setTimeout(() => {
    store.elems.mine.style.setProperty("transform", "");
  }, 50);

  heatPanel.handleMineClick();

  playSound("rock_hit");
  incText({inc: increment, x: event.clientX, y: event.clientY});
  dustParticle({x: event.clientX, y: event.clientY});
}

function handleBuyUpgrade(id) {
  if (store.boughtUpgrades.indexOf(id) !== -1) {
    console.log("Already have this upgrade");
    notify({message: "already_have_upgrade"});
    playSound("menu_discard");
    return;
  }
  let upgrade;
  for (let i = 0; i < upgrades.length; i++) {
    if (upgrades[i].id === id) {
      upgrade = upgrades[i];
    }
  }
  if (store.money < upgrade.price) {
    console.log("Not enough money for this upgrade");
    notify({message: "not_enough_money_upgrade"});
    playSound("menu_discard");
    return;
  }
  store.boughtUpgrades.push(id);
  store.money -= upgrade.price;
  syncUpgrades();
  playSound("upgrade_bought");
  playSound("y0_upgrade_bought");
  fancyNotification({imgSrc: upgrade.img, text: upgrade.title[config.language], liveTime: 3000});
}

function handleWorkersClick(event) {
  const workerItem = event.target.closest(".worker");
  if (!workerItem) {
    return;
  }
  const workerId = +workerItem.getAttribute("data-id");
  let worker;
  for (let i = 0; i < workers.length; i++) {
    if (workers[i].id === workerId) {
      worker = workers[i];
      break;
    }
  }
  let finalPrice;
  let boughtWorker = store.boughtWorkers.find((w) => w.id === workerId);
  if (!boughtWorker) {
    finalPrice = worker.initialPrice;
  } else {
    finalPrice = Math.floor(worker.initialPrice * Math.pow(config.priceIncrement, boughtWorker.quantity));
  }
  if (store.money < finalPrice) {
    notify({message: "not_enough_money_worker"});
    playSound("menu_discard");
    return;
  }
  store.money -= finalPrice;
  if (!boughtWorker) {
    boughtWorker = {id: workerId, quantity: 1};
    store.boughtWorkers.push(boughtWorker);
  } else {
    boughtWorker.quantity += 1;
  }
  // update workers ui
  syncWorkers();
  playSound("menu_select");
}

init();

function loop() {
  processWorkers();
  updateMoney();
}

function updateMoney() {
  if ((!store.elems.moneyBlockCount.innerText) || +store.elems.moneyBlockCount.innerText !== store.money) {
    syncMoney();
  }
}

function processWorkers() {
  let sum = 0;
  for (let i = 0; i < store.boughtWorkers.length; i++) {
    let worker;
    for (let j = 0; j < workers.length; j++) {
      if (workers[j].id === store.boughtWorkers[i].id) {
        worker = workers[j];
        break;
      }
    }
    let multiplier = 1;
    for (let k = 0; k < store.boughtUpgrades.length; k++) {
      let upgrade;
      for (let l = 0; l < upgrades.length; l++) {
        if (store.boughtUpgrades[k] === upgrades[l].id) {
          upgrade = upgrades[l];
          break;
        }
      }
      if (!upgrade) {
        continue;
      }
      if (!upgrade.efficiency_inc) {
        continue;
      }
      if (upgrade.efficiency_worker_id !== store.boughtWorkers[i].id) {
        continue;
      }
      multiplier *= upgrade.efficiency_inc;
    }
    sum += worker.perSecond / config.fps * store.boughtWorkers[i].quantity * multiplier;
  }
  store.money += sum;
  store.perSecond = sum * config.fps;
}

function fillUpgrades() {
  const tmpNode = document.createDocumentFragment();
  upgrades.forEach(upgrade => {
    const upgradeItem = document.createElement("div");
    upgradeItem.className = "upgrade";
    upgradeItem.setAttribute("data-id", upgrade.id);
    upgradeItem.setAttribute("data-action", "buy-upgrade");
    upgradeItem.style.setProperty("background-image", `url(${upgrade.img})`);

    const tooltip = renderTooltip({
      title: upgrade.title[config.language],
      price: upgrade.price,
      description: upgrade.description[config.language]
    });
    upgradeItem.appendChild(tooltip);

    tmpNode.appendChild(upgradeItem);
  });
  return tmpNode;
}

function renderTooltip(data) {
  const tooltip = document.createElement("div");
  tooltip.className = "tooltip";

  const title = document.createElement("div");
  title.className = "tooltip-title";
  title.innerText = data.title;

  const price = document.createElement("div");
  price.className = "tooltip-price";
  price.innerText = `${translate("tooltip_price")} ${data.price}`

  const description = document.createElement("div");
  description.className = "tooltip-description";
  description.innerText = data.description;

  tooltip.appendChild(title);
  tooltip.appendChild(price)
  tooltip.appendChild(description);

  return tooltip;
}

function fillWorkers() {
  const tmpNode = document.createDocumentFragment();
  workers.forEach(worker => {
    const workerItem = document.createElement("div");
    workerItem.className = "worker";
    workerItem.setAttribute("data-id", worker.id);

    const workerImage = document.createElement("div");
    workerImage.className = "worker-image";
    workerImage.style.setProperty("background-image", `url(${worker.img})`);

    const workerName = document.createElement("div");
    workerName.className = "worker-name";
    workerName.innerText = worker.name[config.language];

    const workerPrice = document.createElement("div");
    workerPrice.className = "worker-price";
    workerPrice.innerText = `${translate("tooltip_price")} ${worker.initialPrice}`;

    const workerQuantity = document.createElement("div");
    workerQuantity.className = "worker-quantity";
    workerQuantity.innerText = 0;

    workerItem.appendChild(workerImage);
    workerItem.appendChild(workerName);
    workerItem.appendChild(workerPrice);
    workerItem.appendChild(workerQuantity);

    tmpNode.appendChild(workerItem);
  });

  return tmpNode;
}