import config from "../config";
import boostNotification from "./boostNotification";
import plusIcon from "url:../resources/images/effects/money.png";
import playSound from "./playSound";
import translate from "../translations/translations";

export default (function () {
  let hp;
  let hl;
  const maxHeat = config.maxHeat;
  let timer;
  let store;

  function heatDropHandle() {
    if (store.heat > 0 && store.heat >= config.heatDecrease) {
      store.heat -= config.heatDecrease;
    } else if (store.heat > 0) {
      store.heat = 0;
    }
  }

  function handleHeatIncrease() {
    if (store.heat < maxHeat) {
      store.heat++;
    } else {
      store.money += store.money * 0.1;
      boostNotification({imgSrc: plusIcon, liveTime: 2000, text: translate("more_money_boost")});
      playSound("boost");
      store.heat = 0;
    }
  }

  return {
    initHeatPanel: function (storeObj) {
      store = storeObj;
      const heatPanel = document.createElement("div");
      heatPanel.className = "heat-panel";

      const heatGauge = document.createElement("div");
      heatGauge.className = "heat-gauge";

      const heatLevel = document.createElement("div");
      heatLevel.className = "heat-level";

      heatGauge.appendChild(heatLevel);
      heatPanel.appendChild(heatGauge);
      hp = heatPanel;
      hl = heatLevel;
      timer = setInterval(() => {
        heatDropHandle();
        this.syncHeatPanel();
      }, config.timeToDropHeat);
      this.syncHeatPanel();
      return heatPanel;
    },
    handleMineClick: function () {
      handleHeatIncrease();
      clearInterval(timer);
      timer = setInterval(() => {
        heatDropHandle();
        this.syncHeatPanel();
      }, config.timeToDropHeat);
      this.syncHeatPanel();
    },
    syncHeatPanel: function () {
      const calculatedHeight = store.heat / maxHeat * 100;
      hl.style.setProperty("height", `${calculatedHeight}%`);
    }
  }
})();
