
export default function ({imgSrc, text, liveTime}) {

  console.log(imgSrc);
  const fancyNotificationBack = document.createElement("div");
  fancyNotificationBack.className = "fancy-notification-back";

  const notificationContainer = document.createElement("div");
  notificationContainer.className = "fancy-notification-container";

  const notificationImage = document.createElement("div");
  notificationImage.className = "fancy-notification-image";
  notificationImage.style.setProperty("background-image", `url("${imgSrc}")`);

  const notificationShine1 = document.createElement("div");
  notificationShine1.className = "fancy-notification-shine-1";

  const notificationShine2 = document.createElement("div");
  notificationShine2.className = "fancy-notification-shine-2";

  const notificationText = document.createElement("div");
  notificationText.className = "fancy-notification-text";
  notificationText.innerText = text;

  notificationContainer.appendChild(notificationShine1);
  notificationContainer.appendChild(notificationShine2);
  notificationContainer.appendChild(notificationImage);
  notificationContainer.appendChild(notificationText);
  fancyNotificationBack.appendChild(notificationContainer);

  document.body.appendChild(fancyNotificationBack);
  setTimeout(() => {
    notificationContainer.className = "fancy-notification-container fancy-notification-container--animated";
  }, 0);
  setTimeout(() => {
    fancyNotificationBack.parentNode.removeChild(fancyNotificationBack);
  }, liveTime || 5000);
}