export default function ({imgSrc, text, liveTime}) {

  console.log(imgSrc);
  const boostNotificationBack = document.createElement("div");
  boostNotificationBack.className = "boost-notification-back";

  const notificationContainer = document.createElement("div");
  notificationContainer.className = "boost-notification-container";

  const notificationImage = document.createElement("div");
  notificationImage.className = "boost-notification-image";
  notificationImage.style.setProperty("background-image", `url("${imgSrc}")`);

  const notificationEffect = document.createElement("div");
  notificationEffect.className = "boost-notification-effect";

  const notificationText = document.createElement("div");
  notificationText.className = "boost-notification-text";
  notificationText.innerText = text;

  notificationContainer.appendChild(notificationEffect);
  notificationContainer.appendChild(notificationImage);
  notificationContainer.appendChild(notificationText);
  boostNotificationBack.appendChild(notificationContainer);

  document.body.appendChild(boostNotificationBack);
  setTimeout(() => {
    notificationContainer.className = "boost-notification-container boost-notification-container--animated";
  }, 0);
  setTimeout(() => {
    boostNotificationBack.parentNode.removeChild(boostNotificationBack);
  }, liveTime || 3000);
}